<script>
export default {
  watch: {
    $route() {
      this.$bus.$emit('update_step', 4);
    },
  },
  data() {
    return {
      render_this_order: {
        invoice: {
          type: '',
          carrier: '',
          address: {
            county: '',
            district: '',
            zip_code: '',
            full_address: '',
          },
        },
        recipient: {
          address: {
            county: '',
            district: '',
            zip_code: '',
            full_address: '',
          },
          estimated_time: '',
        },
        payment: {
          method: '',
          credit_card_number: '',
          credit_card_expMonth: '',
          credit_card_expYear: '',
          credit_card_cvc: '',
        },
      },
      invoice_type_options: [
        {
          chinese: '個人電子發票',
          english: 'personal_invoice',
        },
        {
          chinese: '捐贈電子發票',
          english: 'donate_invoice',
        },
        {
          chinese: '公司報帳電子發票',
          english: 'company_invoice',
        },
      ],
      invoice_carrier_options: [
        {
          chinese: '不使用載具',
          english: 'no_carrier',
        },
        {
          chinese: '手機條碼',
          english: 'barcode_carrier',
        },
        {
          chinese: '自然人憑證',
          english: 'natural_carrier',
        },
      ],
      estimated_time_options: [
        {
          chinese: '不指定',
          english: 'default',
        },
        {
          chinese: '上午(08:00~13:00)',
          english: 'morning',
        },
        {
          chinese: '下午(14:00~18:00)',
          english: 'afternoon',
        },
      ],
      payment_method_options: [
        {
          chinese: '信用卡付款',
          english: 'credit_card',
        },
      ],
    };
  },
  props: ['this_order'],
  methods: {
    go_page(pageString) {
      this.$router.push(`/${pageString}`);
    },
    render_data() {
      const original = this.this_order;
      const render = this.render_this_order;
      render.invoice.type = this.invoice_type_options
        .find((item) => item.english === original.invoice.type)
        .chinese;
      render.invoice.carrier = this.invoice_carrier_options
        .find((item) => item.english === original.invoice.carrier)
        .chinese;
      render.invoice.address = JSON.parse(JSON.stringify(original.invoice.address));
      render.recipient.address = JSON.parse(JSON.stringify(original.recipient.address));
      render.recipient.estimated_time = this.estimated_time_options
        .find((item) => item.english === original.recipient.estimated_time)
        .chinese;
    },
    go_next_step() {
      this.go_page('front/checkout/checkout5');
    },
  },
  created() {
    this.render_data();
  },
  mounted() {
    this.$bus.$emit('update_step', 4);
  },
};
</script>
